.search-bg-container {
  width: 60%;
  margin: auto;
}
.landing-main .search-bg-container {
  padding-top: 40px;
}
.landing-main .search-bg-container .form-control {
  height: 40px;
  padding: 8px 15px;
  font-size: 16px;
}
.course-item {
  display: flex;
  padding: 30px 70px;
  justify-content: space-between;
}
.course-item img {
  width: 70px;
  height: 70px;
  margin-right: 25px;
}
.course-item-text {
  flex: 2;
}
.course-item-text h2 {
  margin-top: 0;
}
.course-item-button {
  display: flex;
  align-items: center;
}
.class-room-container {
  padding: 0 70px;
}
.video-js:hover .vjs-big-play-button,
.video-js .vjs-big-play-button:focus,
.video-js .vjs-control-bar {
  background-color: #009878;
}
.video-player {
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
}
.video-player video {
  min-height: 295px;
}
.class-room-header h1 {
  font-size: 16px;
}
.class-room-header h3 {
  margin-top: 10px;
}
.mind-map-container {
  padding: 30px 0;
  overflow: hidden;
  position: relative;
  clear: both;
}
.mind-map-container:after {
  content: '';
  width: 1px;
  top: 30px;
  height: 400px;
  position: absolute;
  right: 0;
  background: #dadae7;
}
.alchemy {
  background: #f9f9fb !important;
}
.alchemy svg {
  background: #f9f9fb !important;
  border: 1px solid #ddd;
  border-radius: 4px;
}
html,
body,
#root {
  margin: 0;
  height: 100%;
  font-family: 'Varela Round', 'Open Sans', sans-serif;
}
body,
h1,
h2,
h3 {
  font-weight: 300;
}
a {
  color: #f9f9fb;
  text-decoration: none;
  font-weight: bold;
}
a:hover {
  color: #ffffff;
  text-decoration: none;
}
.main-container {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}
.main {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: #f9f9fb;
}
.main.landing-main {
  background-color: #009878;
  padding-top: 70px;
}
.header {
  display: flex;
  align-items: center;
  background-color: #009878;
  padding: 5px 70px;
}
.header-title {
  flex: 1;
  font-size: 1.5rem;
  margin: 1rem;
}
.header-search-box {
  flex: 2;
}
.header-date {
  flex: 3;
  text-align: right;
  margin: 1rem;
  color: white;
}
.header-date img {
  border-radius: 50%;
}
.title {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  background-color: #009878;
  color: white;
}
.title-h1 {
  font-weight: 300;
  font-size: 4rem;
  margin: 1rem;
}
.title-logo {
  height: 12rem;
  background-color: white;
  border-radius: 1rem;
  margin: 1rem;
}
.title-h2 {
  font-weight: 300;
  font-size: 2rem;
  margin: .5rem;
}
.footer {
  padding: 0.5rem;
  font-size: 1rem;
  background-color: #1f1f1f;
  text-align: center;
  color: white;
}
.techs-container {
  margin: 1rem;
}
.techs-h2 {
  font-weight: 300;
  font-size: 1.5rem;
}
.techs {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}
.tech {
  height: 15rem;
  width: 15rem;
  border: 1px solid lightgray;
  border-radius: 1rem;
  margin: 1rem;
  padding: 1rem;
}
.tech-logo {
  width: 5rem;
  height: 5rem;
  float: right;
  margin: 0 0 .5rem .5rem;
}
.tech-h3 {
  font-size: 1.5rem;
  margin: 0 0 2rem 0;
}
.waiting {
  position: relative;
  -webkit-transition: all .5s;
  -moz-transition: all .5s;
  -ms-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  pointer-events: none;
}
.waiting:before {
  position: absolute  !important;
  content: '' !important;
  z-index: 14;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: #fff;
  background: rgba(255, 255, 255, 0.75);
  display: block !important;
}
.waiting:after {
  position: absolute  !important;
  z-index: 14;
  top: 300px;
  left: 50%;
  content: url('http://i.imgur.com/OeDSHLY.gif?1') !important;
}
.learn-btn {
  border: 2px solid #009878;
  color: #333;
  padding: 11px;
  min-width: 200px;
  line-height: 16px;
  font-size: 13px;
  text-align: center;
  border-radius: 25px;
  font-weight: 600;
  color: #009878;
  text-transform: uppercase;
  transition: background-color 0.3s, color 0.3s;
}
.learn-btn:hover {
  background: #009878;
  text-decoration: none;
  cursor: pointer;
  color: #eee;
}
.delimiter {
  width: 80%;
  height: 1px;
  background: #dadae7;
  margin: auto;
  text-align: center;
}
.angucomplete-dropdown {
  width: 100%;
}
